.intellForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
}

.intellForm input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 5px;
}

.intellMessagesError {
  color: red;
  font-size: 0.8rem;
  margin: 0;
  padding: 0;
}

.intellMessagesSuccess {
  color: green;
  font-size: 0.8rem;
  margin: 0;
  padding: 0;
}
